<template>
    <div class="power-do-pay">
        <module-pay ref="child" :warn="warn" module="power">
            <mt-cell slot="item" title="" value=""></mt-cell>
        </module-pay>

        <div class="field-action">
            <mt-button type="primary" size="large" @click="doPay"></mt-button>
        </div>

        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
    </div>
</template>

<script>
    import ModulePay from '../module/pay';
    export default {
        name: 'power-do-pay',
        components: {
            ModulePay
        },
        data() {
            return {
                warn: '',
                dialogSmsVerify: {
                    title: '',
                    btnConfirmText: '',
                    btnGetText: ''
                }
            };
        },
        methods: {
            doPay() {
                let _data = Object.assign({}, this.$refs.child.formData);
                if (this.$validate.isEmpty(_data.account)) {
                    this.$msg('');
                    return false;
                }
                if (this.$validate.isEmpty(_data.pwd)) {
                    this.$msg('');
                    return false;
                }
                this.$refs.dialogSmsVerify.show().then((data) => {
                    alert('...');
                    this.$router.push({ name: 'LifeResult', query: { p: -4 } });
                }, (data) => {
                });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$ls.removeItem('power');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .select-account {
        background-color: #fff;
    }
</style>


